import { useSelector } from "react-redux"
import { Link } from "react-router-dom"


function KauppaBack(){

const products = useSelector(state => state.products)
const user = useSelector(state => state.currentUser)

    const kauppaMap = products.map(x =>{
        return(
            <div key={x.tuote}>
            <Link className="linkStyle" to={`/control/kauppa/${x.id}`}><div className="grid-item" >

                <p style={{color:"white"}}>{x.tuote}</p>
                <p style={{color:"white"}}>{x.hinta}€</p>
                <p style={{color:"white"}}>jäljellä {x.määrä}kpl</p>
                <p style={{color:"white"}}>{x.kuvaus}</p>
                <img className="storeImgBig" src={x.kuva} alt={x.tuote} ></img>
            </div>
            </Link>
            </div>
        )
    })

    if(user){
    return(
        <div className="grid-container">
{kauppaMap}
</div>
)
}
else
{
    return(
    <>
    <h2 style={{color:"white"}}>Kirjaudu sisään ensin</h2>
    </>
    )
}

}

export default KauppaBack