//import Navbar from "./components/NavBar";
import Sidebar from './components/Sidebar';
import KauppaBack from './components/KauppaBack';
import GalleriaBack from './components/GalleryBack';
import Events from './components/events';
import {Routes, Route} from "react-router-dom"
import SingleProduct from "./components/singleProducts"
import NewProduct from './components/newProduct';
import Notification from '../components/Notification';
import LoginForm from './components/loginform';
import SingleGallery from './components/singleGallery';
function Dashboard() {
  return (
    <>

   <Sidebar />
    <Notification />
<div className='grid-container'>
<Routes>
<Route path="gallery" element={<GalleriaBack />}  />
<Route path="kauppa/:id" element={<SingleProduct />} />
<Route path="galleria/:id" element={<SingleGallery />} />
<Route path="uusituote" element={<NewProduct />} />
<Route path="/" element={<KauppaBack />}  />
<Route path="event" element={<Events />} />
<Route path="login" element={<LoginForm />} />
</Routes>
</div>

 
    </>
  );
}

export default Dashboard;