import { notice } from "../../reducers/notificationReducer"
import { useState } from "react"
import { useDispatch } from "react-redux"
import { loginUser } from "../../reducers/currentUserReducer"
import { useNavigate } from "react-router-dom";
import { useSelector } from "react-redux"


const LoginForm = () => {
  const [username, setUsername] = useState("")
  const [password, setPassword] = useState("")
  const user = useSelector(state => state.currentUser)
const navigate = useNavigate()
  const dispatch = useDispatch()
  const handleLogin = async (event) => {
    event.preventDefault()
     dispatch(loginUser(username,password))
      setUsername("")
      setPassword("")
      dispatch(notice(`logged in ${username}`,3))
      navigate("/control/kauppa")
    }
const handleUsernameChange = (event) => {
  setUsername(event.target.value)

}
const handlePasswordChange = (event) => {
  setPassword(event.target.value)
}

  return (
    <div>
      <h2>Login</h2>
      <form onSubmit={handleLogin}>
        <div>
          username:
          <input
            type="text"
            value={username}
            name="Username"
            onChange={handleUsernameChange}
            id="username"
          ></input>
        </div>
        <div>
          password:
          <input
            type="password"
            value={password}
            name="Password"
            id="password"
            onChange={handlePasswordChange}
          ></input>
        </div>
        <button type="submit" id="login-button" className="myButton">
          login
        </button>
      </form>
    </div>
  )
}

export default LoginForm
