import { useParams } from "react-router-dom";
import { useSelector } from "react-redux";
import { useState,useEffect } from "react";
import { useDispatch } from "react-redux";
import Button from 'react-bootstrap/Button';
import { deleteProducts,editProduct2 } from "../../reducers/productReducer";
import { useNavigate } from "react-router-dom";
import { notice } from "../../reducers/notificationReducer";
function SingleProduct(){
    const id = useParams().id
    const products = useSelector(state => state.products)
    const product = products.find(product => product.id === id)
    const user = useSelector(state => state.currentUser)
    const [loader,setLoader] = useState(null)
    const [categoria,setCategoria] = useState("")
    const [hinta,setHinta] = useState("")
    const [tuote,setTuote] = useState("")
    const [kuva,setKuva] = useState("")
    const [kuvaus,setKuvaus] = useState("")
    const [määrä,setMäärä] = useState("")

    const dispatch = useDispatch()
    const navigate = useNavigate()

    useEffect(() => {
      if(product){
        setCategoria(product.categoria)
        setHinta(product.hinta)
        setTuote(product.tuote)
        setKuva(product.kuva)
        setKuvaus(product.kuvaus)
        setMäärä(product.määrä)

  }
   },[product]);
    
    const handleDelete = (id) => { 
      
        if (window.confirm("are you sure you want to delete?")) {
           dispatch(deleteProducts(id))
            navigate("/control/")
            dispatch(notice("poistit tuotteen",3))
         }
       }

    function update(id){
      const productObj = {
        ...product,
        categoria: categoria,
        hinta: hinta,
        tuote: tuote,
        kuva: kuva,
        määrä: määrä,
        kuvaus: kuvaus
      }
    console.log(productObj)
     dispatch(editProduct2(productObj,id))
     dispatch(notice("muokkasit tuotetta :)",5))
    }
    const [togTuote,setTogTuote] = useState(true)
    const [togTuote2,setTogTuote2] = useState(true)
    const [togTuote3,setTogTuote3] = useState(true)
    const [togTuote4,setTogTuote4] = useState(true)
    const [togTuote5,setTogTuote5] = useState(true)
    const [togTuote6,setTogTuote6] = useState(true)


    if(product && !loader){
        setLoader(true)
      }
      if(user && loader){
        return(
            <div className="etusivu" style={{color:"white",height:"100%"}}>
               {togTuote ? <h2>{product.tuote}</h2>:<div> <input type="text" value={tuote} onChange={(e) => setTuote(e.target.value)}></input>   <button onClick={() => update(product.id)}>muokkaa</button></div> }
               {togTuote ? <button onClick={()=> setTogTuote(false)}>Editoi</button>: <button onClick={()=> setTogTuote(true)}>Palaa</button>}
               {togTuote2 ? <p>Hinta: {product.hinta}€</p>:<div> <input type="number" value={hinta} onChange={(e) => setHinta(e.target.value)}></input>   <button onClick={() => update(product.id)}>muokkaa</button></div> }
               {togTuote2 ? <button onClick={()=> setTogTuote2(false)}>Editoi</button>: <button onClick={()=> setTogTuote2(true)}>Palaa</button>}
               {togTuote3 ?  <span>Tuote kuvaus: {product.kuvaus}</span>:<div> <input type="text" value={kuvaus} onChange={(e) => setKuvaus(e.target.value)}></input><button onClick={() => update(product.id)}>muokkaa</button></div> }
               {togTuote3 ? <button onClick={()=> setTogTuote3(false)}>Editoi</button>: <button onClick={()=> setTogTuote3(true)}>Palaa</button>}
               {togTuote4 ?  <p>jäljellä: {product.määrä}</p>:<div> <input type="number" value={määrä} onChange={(e) => setMäärä(e.target.value)}></input>   <button onClick={() => update(product.id)}>muokkaa</button></div> }
               {togTuote4 ? <button onClick={()=> setTogTuote4(false)}>Editoi</button>: <button onClick={()=> setTogTuote4(true)}>Palaa</button>}
               {togTuote5 ?  <p>categoria: {product.categoria}</p>:<div> <input type="text" value={categoria} onChange={(e) => setCategoria(e.target.value)}></input>   <button onClick={() => update(product.id)}>muokkaa</button></div> }
               {togTuote5 ? <button onClick={()=> setTogTuote5(false)}>Editoi</button>: <button onClick={()=> setTogTuote5(true)}>Palaa</button>}
               {togTuote6 ? <img src={product.kuva} alt="tuote-kuva"></img>:<div> <input type="text" value={kuva} onChange={(e) => setKuva(e.target.value)}></input>   <button onClick={() => update(product.id)}>muokkaa</button></div> }
               {togTuote6 ? <button onClick={()=> setTogTuote6(false)}>Editoi</button>: <button onClick={()=> setTogTuote6(true)}>Palaa</button>}
               
               
               <Button onClick={() => handleDelete(product.id)}>Poista</Button>
          </div>
        )}
        else {
            return(<div>
                unexpected error happened. please reload page
            </div>)
        }

}
export default SingleProduct