import * as React from 'react';
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import {Link} from "react-router-dom"
import { useDispatch, useSelector } from 'react-redux';
import { logOutter } from '../../reducers/currentUserReducer';
function Sidebar(){
const user = useSelector(state => state.currentUser)
const dispatch = useDispatch()

  return(
    <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
    <Container>
      <Navbar.Brand><Link to="/control/">Tuotteet</Link></Navbar.Brand>
      <Navbar.Toggle aria-controls="responsive-navbar-nav" />
      <Navbar.Collapse id="responsive-navbar-nav">
        <Nav className="me-auto">
          <Navbar.Brand> <Link to="/control/gallery">Galleria</Link> </Navbar.Brand>
          <Navbar.Brand><Link to="/control/event">Events</Link></Navbar.Brand>
          <Navbar.Brand> <Link to="/control/uusituote">Uusi tuote</Link> </Navbar.Brand>
          <Navbar.Brand> <Link to="/">Etusivu</Link> </Navbar.Brand>
        
        </Nav>
        <Nav>
          <Navbar.Brand>
         {user ? <button className='btn2' onClick={() => dispatch(logOutter())}>Kirjaudu ulos</button> : <Link to="/control/login">Kirjaudu sisään </Link>}
          </Navbar.Brand>
        </Nav>
      </Navbar.Collapse>
    </Container>
  </Navbar>
)}
export default Sidebar;